html,body{
    margin: 0;
    height: 100%;
}
.contain{
    /*此时整个处于垂直居中状态*/
    display: block;
    margin: 0 auto;
    padding: 3em;
    height: auto;
    width: 90%;
}

/*第一行的样式*/
.first{
    display: flex;
}
.firstName{
    flex:1;
    /*width: auto;*/
    text-align: left;
    font-size: 30px;
    font-family: SimHei, serif;
    /*box-shadow: #666 0 0 2px;*/
}
.first-align-right{

    text-align: right;
}
.fist-link{
    margin: 0 auto;
    padding: 1em;
}

/*第二行的样式*/
.second{
    margin-top: 3em;
    display: flex;
}
.main-logo{
    margin-left: 4em;
    width: 5em;
    height: 5em;
}
.search-input{
    width: 30em;
}
.search-button{
    height: 2em;
}
.search-row{
    margin: 0 auto;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1em 0;
}
.some-class{
    margin: 0 auto;
}
.third{
    margin-top: 8em;
}
.tips{
    text-align: left;
}
.icon-img{
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50% ;
    height: 2em;
    width: 2em;
    font-size: 60px;
    box-shadow: #666 0 0 2px;
}
.make-way{
    display: flex;
    align-items: center;
    justify-content: center;
}
.logo-icon{
    margin-right: 4em;
}